.plans__container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 3rem;
}

.plan h3 {
	margin-bottom: 0.8rem;
}

.plan small {
	margin-bottom: 0.8rem;
}

.plan h1 {
	color: var(--color-primary-variant);
	display: inline;
}

.plan h2 {
	color: var(--color-gray-400);
	display: inline;
}

.plan h4 {
	margin-top: 2rem;
}

.plan p {
	margin-top: 1rem;
	color: var(--color-gray-100);
}

.plan p.disabled {
	color: var(--color-gray-400);
}

.plan button {
	margin-top: 3rem;
	cursor: pointer;
}



/* media queries */

@media screen and (max-width: 1024px) {
	.plans__container {
		grid-template-columns: 1fr 1fr;
		grid: 2rem;
	}
}

@media screen and (max-width: 600px) {
	.plans__container {
		width: 90%;
		grid-template-columns: 1fr;
	}
	.plan{
		width: 90%;
		margin-inline: auto;
	}
}